@import url("https://fonts.googleapis.com/css2?family=Alkatra:wght@400;500;600;700&family=Caveat:wght@400;500&family=Cinzel:wght@400;500;600&family=Clicker+Script&family=Fasthand&family=Lobster&family=Marck+Script&family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,600&family=Poppins:ital,wght@0,400;0,600;1,200;1,400;1,500&family=Redacted+Script:wght@300&family=Ubuntu:ital,wght@0,300;0,400;1,300;1,400&display=swap");
.footer {
  border-top-left-radius: 125px;
  font-size: 13px;
  line-height: 20px;
  font-family: poppins;
  width: 100%;
  margin: auto;
  box-shadow: 10px 8px 32px 0 rgba(32, 20, 30, 0.37);
}

.footer .row {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 30% 20% 15% 15% 20%;
  flex-wrap: nowrap;
  position: relative;
}

.footer .col {
  color: #000;
}

.footer .col img {
  width: 100px;
  margin: 10px 50px 10px;
}
.footer .col h3 {
  margin-top: 30px;
  margin-bottom: 20px;
  padding-top: 10px;
  color: #000;
}

.footer .col h3::before {
  content: "";
  background: rgb(247, 100, 74);
  width: 50px;
  height: 2px;
  position: absolute;
  top: 60px;
}

.footer .col h4 {
  width: 35px;
  padding: 10px 0;
}

footer .email-id {
  width: fit-content;
  border-bottom: 1px solid #dd195a;
  margin: 20px 0;
}

.footer ul li a {
  color: #000000;
  padding: 0;
  margin: 0;
  transition: all 0.3s ease;
}
.footer ul li a:hover {
  color: rgb(247, 100, 74);
}

.footer .col form {
  padding-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #000000;
  margin-bottom: 20px;
  width: 100%;
}
.footer col form .bi-envelope {
  font-size: 18px;
  margin-right: 20px;
}
.footer .col form input {
  width: 100%;
  background: transparent;
  color: ccc;
  border: none;
  outline: none;
  color: #000000;
  font-family: poppins;
}

.footer .col form button {
  border: none;
  background: transparent;
  outline: none;
  color: #020202;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
  transition: all 0.3s ease;
}

.footer .col form button:hover {
  color: rgb(247, 100, 74);
  transform: scale(1.09976);
}

.footer .col .social-icons .bi {
  cursor: pointer;
  margin-right: 20px;
  line-height: 40px;
  text-align: center;
  font-size: 20px;
}
.footer .col form .bi-envelope {
  font-size: 18px;
  margin-right: 20px;
}

.footer .col .social-icons .bi:hover {
  color: rgb(247, 100, 74);
}
.footer hr {
  width: 90%;
  border: 0;
  border-bottom: 1px solid #0f0f0f;
  margin: 10px auto;
}
.footer .copyright {
  text-align: center;
  color: #000;
  padding-bottom: 20px;
}

.footer .copyright span {
  color: rgb(247, 100, 74);
}

.footer .app img {
  margin-top: 20px;
  cursor: pointer;
  text-align: center;
  margin-left: 10px;
}

.footer .col-1 p{
    text-align: center;
    padding: 10px 20px;
}

@media screen and (max-width:1150px) {
    .footer .row {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }

.footer .col-1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

    .footer .col h3::before {
        display: none;
    }

    .footer .col h3{
        text-decoration: underline 2px solid rgb(241, 50, 16);
        padding-bottom: 2px;
    }

    .footer .col{
    margin: 20px;
}
    .footer .col-1 p{
    text-align: center;
    padding: 0;
}
    
}

@media screen and (max-width:850px) {
    .footer .row {
        
        display: grid;
        grid-template-columns: 50% 50%;
        justify-content: space-evenly;
        align-items: center;
        margin-left: 10px 40px;
        
    }
.footer .col{
    margin: 20px;
    justify-content: space-evenly;
}

    .footer .col-1 p{
    text-align: center;
    padding: 0;
}
}